import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RedComponent } from './red/red.component';
import { BlueComponent } from './blue/blue.component';
import { GreenComponent } from './green/green.component';
import { YellowComponent } from './yellow/yellow.component';
import { GreyComponent } from './grey/grey.component';
import { OtherComponent } from './other/other.component';
import { SidebarComponent } from './sidebar/sidebar.component';

const routes: Routes = [
  { path: "", component: SidebarComponent },
  { path: "red", component: RedComponent},
  { path: "blue", component: BlueComponent},
  { path: "green", component: GreenComponent},
  { path: "yellow", component: YellowComponent},
  { path: "grey", component: GreyComponent},
  { path: "other", component: OtherComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }

// Custom Routing Component which holds array of component for routing
export const routingComponents = [RedComponent, BlueComponent, GreenComponent, YellowComponent, GreenComponent, GreyComponent, OtherComponent];
