import { Component, OnInit } from '@angular/core';
import { ColorsService } from '../service/colors.service';

@Component({
  selector: 'app-yellow',
  templateUrl: './yellow.component.html',
  styleUrls: ['./yellow.component.css']
})
export class YellowComponent implements OnInit {

  public colors = [];
  constructor(private colorService: ColorsService) { }

  ngOnInit() {
    this.colorService.getColor()
      .subscribe(data => this.colors = data);
  }

}
