import { Component, OnInit } from '@angular/core';
import { ColorsService } from '../service/colors.service';

@Component({
  selector: 'app-grey',
  templateUrl: './grey.component.html',
  styleUrls: ['./grey.component.css']
})
export class GreyComponent implements OnInit {

  public colors = [];
  constructor(private colorService: ColorsService) { }

  ngOnInit() {
    this.colorService.getColor()
      .subscribe(data => this.colors = data);
  }

}
